body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-arrows;
  position: absolute;
  top: calc(50% - 20vmin);
  left: calc(50% - 1.5vmin);
}

.stopwatch__minutes:hover {
  animation-play-state: paused;
}

.stopwatch__seconds {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-arrows;
  position: absolute;
  top: calc(50% - 38vmin);
  left: calc(50% - .75vmin);
}

.stopwatch__seconds:hover {
  animation-play-state: paused;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

.stopwatch--speed-up .stopwatch__minutes {
  animation: 600s steps(60, end) infinite rotate-arrows;
}

.stopwatch--speed-up .stopwatch__minutes:hover {
  animation-play-state: paused;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation: 10s linear infinite rotate-arrows;
}

.stopwatch--speed-up .stopwatch__seconds:hover {
  animation-play-state: paused;
}

@keyframes rotate-arrows {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
